///<reference path="../../services/jweixin.d.ts"/>
///<reference path="../../services/user_defined.d.ts"/>
import { Injectable } from '@angular/core';
import { HttpServicesProvider } from '../../providers/http-services/http-services';
@Injectable()
export class WechatProvider {

  constructor(private httpService: HttpServicesProvider) {
  }
  wxConfig(callback) {
    if (!global_wxFunciton.isloadWxConfig()) {
      let url = location.href.split('#')[0];//url
      let api = 'wechat/wechatParam.wxpaydo';
      this.httpService.requestData(api, (data) => {
        wx.config({
          debug: false,////debug
          appId: data.appid,//appId
          timestamp: data.timestamp,//
          nonceStr: data.nonceStr,//
          signature: data.signature,//
          jsApiList: [//JS
            'checkJsApi',//JS
            'onMenuShareTimeline',//
            'onMenuShareAppMessage',//
            'onMenuShareQQ',
            'onMenuShareQZone',
            'uploadImage',  //
            'chooseWXPay',
            'chooseImage'
          ]
        });
        
      }, { url: url });
    }else{
      callback();
    }
    wx.ready(() => {
      global_wxFunciton.setWxConfig();
      callback();
    });
  }
}



